import React, { useState, useMemo } from 'react';
import api from '../../services/api';

import { RiSave3Fill } from 'react-icons/ri'

import camera from '../../assets/camera.svg';

import './styles.css';


export default function New({ history }) {
  let [thumbnail, setThumbnail] = useState(null);
  let [category, setCategory] = useState("0");

  const [alert, setAlert] = useState('');
  const [error, setError] = useState('');

  const [requiredThumb, setRequiredThumb] = useState(false);
  const [requiredName, setRequiredName] = useState(false);
  const [requiredCat, setRequiredCat] = useState(false);
  const [requiredPrice, setRequiredPrice] = useState(false);


  const user_token = localStorage.getItem('user_token');

  if (!user_token)
    history.push('/');

  const preview = useMemo(() => {
    return thumbnail ? URL.createObjectURL(thumbnail) : null;
  }, [thumbnail])


  const getHeightAndWidthFromDataUrl = dataURL => new Promise(resolve => {
    const img = new Image()
    img.onload = () => {
      resolve({
        height: img.height,
        width: img.width
      })
    }
    img.src = dataURL
  })


  async function handleSubmit(event) {
    event.preventDefault();

    let data = {
      thumbnail: event.target[0].files[0],
      name: event.target.name.value,
      category: event.target.category.value,
      price: event.target.price.value
    }


    if (!data.thumbnail) {
      setRequiredThumb(true)
    } else {
      setRequiredThumb(false)
      const blobImage = window.URL.createObjectURL(data.thumbnail)

      let dim = await getHeightAndWidthFromDataUrl(blobImage);
      console.log(dim)

      if (dim.height !== 250 && dim.width !== 250) {
        setAlert("A resolu√ß√£o da foto deve ser de 250x250 pixels.")
        return
      }
    }

    if (!data.name)
      setRequiredName(true)
    else
      setRequiredName(false)

    if (data.category === "0")
      setRequiredCat(true)
    else
      setRequiredCat(false)

    if (!data.price)
      setRequiredPrice(true)
    else
      setRequiredPrice(false)

    if (data.price.length > 0) {
      let regex = /^[-]?\d+$/
      if (!regex.test(data.price)) {
        setAlert("O valor da di√°ria deve ser num√©rico.")
        return
      }
    }

    if (data.thumbnail && data.name && data.category !== "0" && data.price) {
      // setRequiredThumb(false)
      // setRequiredName(false)
      // setRequiredCat(false)
      // setRequiredPrice(false)

      setAlert("")

      const payload = new FormData();
      const user_token = localStorage.getItem('user_token');

      payload.append('thumbnail', data.thumbnail);
      payload.append('name', data.name);
      payload.append('category', data.category);
      payload.append('price', data.price);

      api.post('/equipos', payload, {
        headers: { user_token }
      }).then(() => {
        history.push('/dashboard');
      }).catch((error) => {
        console.log(error.response)
        if (error.response.data.code === 1003) {
          setError("An√∫ncio j√° existe :/")
          return
        }
      });
    }

  }

  return (
    <form onSubmit={handleSubmit} id="equipoForm">
      <label
        id="thumbnail"
        style={{ backgroundImage: `url(${preview})` }}
        className={thumbnail ? 'has-thumbnail' : ''}
      >
        <input type="file" onChange={event => setThumbnail(event.target.files[0])} />
        <img src={camera} alt="Select img" />
      </label>

      {requiredThumb &&
        <span className="alert-form">Adicione uma foto no seu an√∫ncio.</span>
      }

      <label htmlFor="name">EQUIPAMENTO *</label>
      <input
        id="name"
        placeholder="Descreva o seu equipamento"
      />

      {requiredName &&
        <span className="alert-form">Informe a descri√ß√£o do an√∫ncio.</span>
      }

      <label htmlFor="category">CATEGORIA *</label>
      <select id="category" value={category} onChange={event => setCategory(event.target.value)}>
        <option value="0" disabled>Escolha uma categoria</option>
        <option value="Amplificadores">Amplificadores</option>
        <option value="√Åudio e Tecnologia">√Åudio e Tecnologia</option>
        <option value="Cordas">Cordas</option>
        <option value="Baterias e Percuss√£o">Baterias e Percuss√£o</option>
        <option value="Teclas">Teclas</option>
        <option value="Sopros">Sopros</option>
        <option value="Outros">Outros</option>
      </select>

      {requiredCat &&
        <span className="alert-form">Escolha uma categoria.</span>
      }

      <label htmlFor="price">VALOR DA DI√ÅRIA <span>(n√£o use virgulas)</span></label>
      <input id="price" name="price" placeholder="Valor cobrado por dia" />

      {requiredPrice &&
        <span className="alert-form">Informe o valor da di√°ria.</span>
      }

      {alert.length > 0 &&
        <div className="alert alert-dark" >{alert}</div>
      }

      {error.length > 0 &&
        <div className="alert alert-error" >{error}</div>
      }

      <button type="submit" className="btn btn-success"><RiSave3Fill /> Cadastrar</button>
    </form>
  )
}