import React, { useState } from 'react';
import api from '../../services/api';

import './styles.css';

export default function Login({ history }) {
  const [fullName, setFullName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPass] = useState('');
  const [alert, setAlert] = useState('');
  const [error, setError] = useState('');
  const [signUp, setSignUp] = useState('Cadastrar');


  async function handleSubmit(event) {
    event.preventDefault();

    localStorage.clear();

    const regex = /\S+@\S+\.\S+/;

    if (!fullName) {
      setAlert('Oops. Informe seu nome completo!')
      return
    }

    if (!regex.test(email) || !email) {
      setAlert('Oops. Informe um email v√°lido!')
      return
    }

    if (!password) {
      setAlert('Oops. Informe sua senha secreta!')
      return
    }

    try {
      setSignUp('Aguarde...');

      const response = await api.post('/signup', { name: fullName, email, password });

      const { _id } = response.data;

      if (!_id) {
        setAlert('Oops. Algo errado ocorreu!')
      } else {
        localStorage.setItem('user_token', _id);

        setTimeout(() => {
          history.push('/dashboard');
        }, 500);
      }

      // setTimeout(() => {
      //   history.push('/dashboard');
      // }, 3000);

    } catch (error) {
      if (error.response.data.code === 1001) {
        setError("ü§≠ Email j√° cadastrado.")
        setSignUp('Cadastrar');
        return
      }
    }
  }

  return (
    <>
      <p>
        Cadastre-se para an√∫nciar!</p>

      <form onSubmit={handleSubmit}>

        <input
          id="fullName"
          type="text"
          placeholder="Seu nome completo"
          value={fullName}
          onChange={event => setFullName(event.target.value)}
        />

        <input
          id="email"
          type="text"
          placeholder="Seu melhor email"
          value={email}
          onChange={event => setEmail(event.target.value)}
        />

        <input
          id="password"
          type="password"
          placeholder="Sua senha secreta"
          value={password}
          onChange={event => setPass(event.target.value)}
        />

        <div className="survey-radio">
          <input type="radio" value="0" name="customer-radio" id="player" />
          <label htmlFor="player">Sou musico</label>
          <input type="radio" value="1" name="customer-radio" id="dealer" />
          <label htmlFor="dealer">Sou loja</label>
        </div>

        <div className="survey-check">

          <p className="subtitle">Equipos de sua prefer√™ncia:</p>

          <input type="checkbox" value="0" name="acordeon" id="acordeon" />
          <label htmlFor="acordeon">Acordeon</label>

          <input type="checkbox" value="0" name="bass" id="bass" />
          <label htmlFor="bass">Contra Baixo</label>

          <input type="checkbox" value="0" name="guitar" id="guitar" />
          <label htmlFor="guitar">Guitarra</label>

          <input type="checkbox" value="0" name="banjo" id="banjo" />
          <label htmlFor="banjo">Banjo</label>

          <input type="checkbox" value="0" name="piano" id="piano" />
          <label htmlFor="piano">Piano</label>

          <input type="checkbox" value="0" name="violao" id="violao" />
          <label htmlFor="violao">Viol√£o</label>

          <input type="checkbox" value="0" name="conga" id="conga" />
          <label htmlFor="conga">Conga</label>

          <input type="checkbox" value="0" name="bateria" id="bateria" />
          <label htmlFor="bateria">Bateria</label>

          <input type="checkbox" value="0" name="mic" id="mic" />
          <label htmlFor="mic">Microfone</label>

          <input type="checkbox" value="0" name="sax" id="sax" />
          <label htmlFor="sax">Saxofone</label>

          <input type="checkbox" value="0" name="clarin" id="clarin" />
          <label htmlFor="clarin">Clarinete</label>

          <input type="checkbox" value="0" name="outros" id="outros" />
          <label htmlFor="outros">Outros</label>

        </div>


        {alert.length > 0 &&
          <div className="alert alert-dark" >{alert}</div>
        }

        {error.length > 0 &&
          <div className="alert alert-error" >{error}</div>
        }

        <button className="btn btn-primary" type="submit">{signUp}</button>

        <small className="link">J√° tem conta? <a href="/">Fa√ßa Login</a></small>
      </form>
    </>
  )
}