import React, { useState } from 'react';
import api from '../../services/api';


export default function Login({ history }) {
  const [email, setEmail] = useState('');
  const [password, setPass] = useState('');
  const [alert, setAlert] = useState('');
  const [signIn, setSignIn] = useState('Entrar');

  const user_token = localStorage.getItem('user_token');

  if (user_token)
    history.push('/dashboard');


  async function handleSubmit(event) {
    event.preventDefault();

    const regex = /\S+@\S+\.\S+/;

    if (!regex.test(email) || !email) {
      setAlert('Oops. Informe um email v√°lido!')
      return
    }

    if (!password) {
      setAlert('Oops. Informe sua senha secreta!')
      return
    }

    try {
      setSignIn('Aguarde...');
      const response = await api.post('/sessions', { email, password });

      const { user_token } = response.data;

      if (!user_token) {
        setAlert('Oops. Algo errado ocorreu!')
      } else {
        localStorage.setItem('user_token', user_token);

        setTimeout(() => {
          history.push('/dashboard');
        }, 500);
      }

    } catch (error) {
      setAlert('Usu√°rio e/ou senha inv√°lidos.')

      setTimeout(() => {
        setSignIn('Entrar')
      }, 300);
    }
  }

  return (
    <>
      <p>
        Fa√ßa parte da maior comunidade de aluguel de equipamentos musicais!</p>

      <form onSubmit={handleSubmit}>
        <input
          id="user_email"
          type="text"
          placeholder="Seu email"
          value={email}
          onChange={event => setEmail(event.target.value)}
        />

        <input
          id="user_password"
          type="password"
          placeholder="Sua senha secreta"
          value={password}
          onChange={event => setPass(event.target.value)}
        />

        {alert.length > 0 &&
          <div className="alert alert-dark" >{alert}</div>
        }

        <button className="btn btn-primary" type="submit">{signIn}</button>

        <small className="link">N√£o tem conta? <a href="/signup">Cadastre-se</a></small>
      </form>
    </>
  )
}