import React, { useEffect, useState, useMemo } from 'react';
import { Link } from 'react-router-dom';
import socketio from 'socket.io-client';
import api from '../../services/api';

import { VscSignOut } from 'react-icons/vsc'
import { GiGuitar } from 'react-icons/gi'
// import { GoPlay } from 'react-icons/go'
import { BsSearch } from 'react-icons/bs'

// import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
// import { Carousel } from "react-responsive-carousel";

import './styles.css';

import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css

import oops from '../../assets/oops.svg';
import camera from '../../assets/default.svg';

export default function Dashboard({ history }) {
  const [equipos, setEquipos] = useState([]);
  const [requests, setRequests] = useState([]);
  const [nameParam, setNameParam] = useState([]);
  const [block, setBlock] = useState([]);
  const [alert, setAlert] = useState([]);
  const user_token = localStorage.getItem('user_token');

  if (!user_token)
    history.push('/');

  const socket = useMemo(() => socketio(process.env.REACT_APP_API, {
    query: { user_token },
  }), [user_token]);

  useEffect(() => {
    socket.on('renting_request', data => {
      setRequests([...requests, data]);
    })
  }, [requests, socket]);

  useEffect(() => {
    setAlert("")
    api.get('/equipos', {
      headers: { user_token: localStorage.getItem('user_token') }
    }).then(response => {

      if (response.data.length === 0) {
        setAlert("Nenhum an√∫ncio cadastrado!")
      }

      if (response.data.length >= 4) {
        setBlock(true)
      } else {
        setBlock(false)
      }

      setEquipos(response.data);
    })
  }, []);

  async function handleAccept(id) {
    await api.post(`/renting/${id}/approvals`);

    setRequests(requests.filter(request => request._id !== id));
  }

  async function handleReject(id) {
    await api.post(`/renting/${id}/rejections`);

    setRequests(requests.filter(request => request._id !== id));
  }

  async function onImageError(ev) {
    ev.target.src = camera
  }

  async function handleSearch() {
    setAlert("")
    api.get('/equipos', {
      headers: { user_token: localStorage.getItem('user_token') },
      params: { name: nameParam }
    }).then(response => {

      if (response.data.length === 0) {
        setAlert("Nenhum an√∫ncio encontrado!")
      }

      if (response.data.length >= 4) {
        setBlock(true)
      } else {
        setBlock(false)
      }

      setEquipos(response.data)
    })
  }

  async function handleLogout() {
    localStorage.clear();
  }

  async function handleRemoveEquipo(equipo) {

    confirmAlert({
      title: 'Aten√ß√£o',
      message: `O an√∫ncio "${equipo.name}", ser√° apagado do sistema. Voc√™ est√° certo disso?`,
      buttons: [
        {
          label: 'Sim',
          onClick: async () => {
            const user_token = localStorage.getItem('user_token');
            await api.delete(`/equipos/${equipo._id}`, {
              headers: { user_token }
            });

            setEquipos(equipos.filter(e => e._id !== equipo._id));
          }
        },
        {
          label: 'N√£o',
          onClick: () => { }
        }
      ]
    });
  }

  return (
    <>
      <div className="dashboard">

        <nav className="nav">
          <div className="welcome">
            <h1>Meus an√∫ncios</h1>
          </div>
          <div className="actions">
            <Link to="/" className="do-Logout" onClick={() => handleLogout()}>
              <VscSignOut size="25" color="#444" />
            </Link>
          </div>
        </nav>

        <ul className="notifications">
          {requests.map(request => (
            <li key={request._id}>
              <p>
                <strong>{request.user.email}</strong> deseja alugar o equipamento: <strong>{request.equipo.name}</strong> : <strong>{request.date}</strong>
              </p>
              <button className="accept" onClick={() => handleAccept(request._id)}>ACEITAR</button>
              <button className="reject" onClick={() => handleReject(request._id)}>REJEITAR</button>
            </li>
          ))}
        </ul>

        <form id="search">
          <div className="search-box">
            <div className="search">
              <input
                type="text"
                id="equipoName"
                className="searchTerm"
                placeholder="Buscar..."
                value={nameParam}
                onChange={e => setNameParam(e.target.value)}
              />
              <button type="button" className="searchButton" onClick={handleSearch}>
                <BsSearch />
              </button>
            </div>
          </div>
        </form>




        <table className="equipo-table">
          {/* <thead>
            <tr>
              <th>Foto</th>
              <th>An√∫ncio</th>
              <th>Di√°ria</th>
              <th></th>
            </tr>
          </thead> */}
          <tbody>
            {equipos.map(equipo => (
              <tr key={equipo._id}>
                <td><img src={equipo.thumbnail_url} alt={equipo.name} onError={onImageError} width="50px" className="thumbnail" /></td>
                <td>{equipo.name}</td>
                <td>{equipo.price ? `R$${equipo.price}/dia` : 'GRATUITO'}</td>
                <td><i className="material-icons delete-icon" onClick={() => handleRemoveEquipo(equipo)}>delete_forever</i></td>
              </tr>
            ))}
          </tbody>
        </table>




        {/* <Carousel showArrows={true} showThumbs={false} showStatus={false}>
          {equipos.map(equipo => (
            <div onClick={() => handleRemoveEquipo(equipo)}>
              <img src={equipo.thumbnail_url} onError={onImageError} width="250px" />
              <p className="legend">{equipo.name} - {equipo.price ? `R$${equipo.price}/dia` : 'GRATUITO'}</p>
            </div>
          ))}
        </Carousel> */}

        {/* <ul className="equipo-list">
          {equipos.map(equipo => (
            <li key={equipo._id}>
              <img src={equipo.thumbnail_url} height="100px" onError={onImageError} />
              <strong>{equipo.name}</strong>
              <span>{equipo.price ? `R$${equipo.price}/dia` : 'GRATUITO'}</span>
              <i className="material-icons delete-icon" onClick={() => handleRemoveEquipo(equipo)}>delete_forever</i>
            </li>
          ))}
        </ul> */}

        <div className="buttons">

          {alert.length > 0 &&
            <div className="alert alert-oops" >
              <img src={oops} alt="oops" />
              <div>{alert}</div>
            </div>
          }

          {block === false &&
            <Link to="/equipos">
              <button className="btn btn-primary btn-full"><GiGuitar /> Criar an√∫ncio</button>
            </Link>
          }

          {block === true &&
            <button className="btn btn-dark btn-full">Voc√™ pode cadastrar at√© 4 Equipos</button>
          }

        </div>

        {/* <Link to="/catalog">
          <button className="btn btn-success ml-5"><GoPlay /> Cat√°logo</button>
        </Link>

        <Link to="/">
          <button className="btn btn-dark btn-logout ml-5"><VscSignOut /> Sair</button>
        </Link> */}

      </div>
    </>
  )
}